/*клиенты*/
.icons__container {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    height: 200px;
    align-items: center;
    img {
        width: 100%;
    }
}
@media (max-width: 768px) {
    .icons__container {
        img {
            width: 75%;
        }
    }
}