.overlay {
    opacity: 0;
    visibility: hidden;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    z-index: 20;
    transition: .3s all;
 }

 .modal {
    opacity: 0;
    visibility: hidden;
    width: 100%;
    max-width: 60%;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 30; 
    box-shadow: 0 3px 10px -.5px rgba(0, 0, 0, .2); 
    text-align: center;
    padding: 30px;
    border-radius: 3px;
    background-color: #fff;
    transition: 0.3s all;
    &__cross {
      width: 15px;
      height: 15px;
      position: absolute;
      top: 20px;
      right: 20px;
      fill: #444;
      cursor: pointer;
      &:hover {
         fill: #10c9c3;
         transition: .3s;
      }
    }
    &__title {
       margin-bottom: 20px;
      @extend .services__item-name;
    }
    &__desc {
       margin-top: 20px;
       @extend .services__item-description;
    }
 }
 @media (max-width: 768px) {
    .modal {
       max-width: 80%;
    }
 }
 
 .modal.active,
 .overlay.active{
    opacity: 1;
    visibility: visible;
 }
 

 
